[class*="__container"] {
  @include maxWmaxH(1180px, inherit);

  padding: 0 25px;
  margin: 0 auto;
  width: 100%;
}

.title {
  @include text(400, 36px, $dark);

  &--center {
    text-align: center;
  }

  &--right {
    text-align: right;
  }
}

.show {
  display: block;
}

.hide {
  display: none;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;

  @keyframes fade {
    from {
      opacity: 0.1;
    }

    to {
      opacity: 1;
    }
  }
}
